export var getMonthDays = function (year, month) {
    var days = [];
    var date = new Date(year, month, 1);
    while (date.getMonth() === month) {
        days.push(new Date(date));
        date.setDate(date.getDate() + 1);
    }
    return days;
};
export var pad = function (n) { return n.toString().padStart(2, '0'); };
export var formatDate = function (date) {
    return "".concat(date.getFullYear(), "-").concat(pad(date.getMonth() + 1), "-").concat(pad(date.getDate()));
};
